function renderData(res) {
    var urlParams = new URLSearchParams(window.location.search);
    var showResults = urlParams.get('Sector');
    var showResults1 = urlParams.get('Country');
    var showResults2 = urlParams.get('Region');
    var showResults3 = urlParams.get('willingness');
    var showResults4 = urlParams.get('quality');
    var showResults5 = urlParams.get('lsfirstdegree');
    var showResults6 = urlParams.get('search');
  
    return (
      (showResults || showResults1 || showResults2 || showResults3 || showResults4 || showResults5 || showResults6) ? 
      <div className="list-item" key={res._id}>
        <ExpandCollapse previewHeight="390px" expandText="Show more">
          <table className="maintable">
            <tr>
              <td className="padright" valign="top" rowSpan="2">
              
{res.avatar && res.avatar != "NULL" ? (
                  <img
                    width="50px"
                    src={res.avatar.replace(
                      "t_api_images/w_50,h_50,c_scale/",
                      "t_api_images/"
                    )}
                  />
                ) : res.url.substring(0, 6) == "Person" ? (
                  <img
                    width="50px"
                    src="https://cdn.icon-icons.com/icons2/510/PNG/512/person_icon-icons.com_50075.png"
                  />
                ) : (
                      <img
                        width="50px"
                        src="https://visualpharm.com/assets/302/Company-595b40b75ba036ed117d5a00.svg"
                      />
                    )}
              </td>
              <td className="namefont">
                <a target="_parent" href={"https://lscportal.azurewebsites.net/webpages/RMS/" + res.url}>{res.name}</a>
                <div className="icondiv">
              {res.crunchbase &&
                    res.crunchbase != "NULL" &&
                    res.cruncbase != "nbsp;" ? (
                      res.url.substring(0, 6) == "Person" ? (
                        <a
                          target="_blank"
                          href={"http://crunchbase.com/person/" + res.crunchbase}
                        >
                          <img
                            src={
                              "https://www.theteams.kr/includes/uploads/company_profile/Crunchbase.png"
                          }
                          />
                        </a>
                      ) : (
                          <a
                            target="_blank"
                            href={
                              "http://crunchbase.com/organization/" + res.crunchbase
                          }
                          >
                            <img
                              src={
                                "https://scontent-sjc3-1.xx.fbcdn.net/v/t1.0-0/p370x247/14238104_10154509878326171_7795750764042171439_n.png?_nc_cat=100&_nc_ht=scontent-sjc3-1.xx&oh=193da79d7563483ba878729156683717&oe=5D138A28"
                            }
                              alt={""}
                            />
                          </a>
                        )
                    ) : null}

              {res.linkedin && res.linkedin != "NULL" ? (
                <a target="_blank" href={res.linkedin}>
                  <img
                    src={
                      "https://upload.wikimedia.org/wikipedia/commons/c/ca/LinkedIn_logo_initials.png"
              }
                    alt={""}
                  />
                </a>
              ) : null}

              {res.facebook && res.facebook != "NULL" ? (
                <a target="_blank" href={res.facebook}>
                  <img
                    src={
                      "https://upload.wikimedia.org/wikipedia/commons/c/cd/Facebook_logo_%28square%29.png"
              }
                    alt={""}
                  />
                </a>
              ) : null}
                </div>
              </td>
            </tr>

            <tr className="rowup">
              <td>
                <div className="jobtitlefont">
                                    {res.url.substring(0, 6) == "Person" && res.jobs_current[0] != null
                                        ? res.jobs_current[0].job_title + " at "
                  : null}
                                    {res.url.substring(0, 6) == "Person" && res.jobs_current[0] != null
                                        ? <a target="_parent" href={"https://lscportal.azurewebsites.net/webpages/RMS/Company.aspx?SearchCompanyName=" + res.jobs_current[0].company_uuid}>{res.jobs_current[0].company_name}</a>

                : null}
                </div>
                                <div>
              
             
                  {res.categories.length > 0 ? (
                    <div className="card-tags">
                  {res.categories.slice(0, 7).map(topic => (
                    <span className="card-tag" key={`${res.name}-${topic}`}>
                      #{topic}
                    </span>
                  ))}
                </div>
              ) : null}
            </div>
          </td>
        </tr>
      </table>
    </ExpandCollapse>
  </div> : null
);
                    }



